const {ccclass, property} = cc._decorator;

@ccclass
export default class ScoreCell extends cc.Component {

    @property(cc.Label) scoreLabel: cc.Label = null;
    @property(cc.Label) timeLabel: cc.Label = null;

    reloadData(score: number, ctime: string)  {

        cc.log('$$$$$$$$$$$$', score, ctime)
        this.scoreLabel.string = score.toString() + 'åˆ†'
        this.timeLabel.string = ctime
    }

    
}
